:root {
  --primary-color: #6d6875;
  --secondary-color: #b5838d;
  --tertiary-color: #e5989b;
  --quarternary-color: #ffb4a2;
  --quinary-color: #ffcdb2;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'poppins', sans-serif;
  color: #212121;
}

.img-res {
  display: block;
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

i.fab.fa-linkedin,
i.fab.fa-github-square,
i.fas.fa-envelope-square {
  color: var(--primary-color);
  margin: 20px;
}

i.fas.fa-link.fa-2x,
i.fas.fa-github.fa-2x{
  margin: 20px;
}


.form-control {
  padding-left: 10px;
  border: 0;
  border-bottom: 3px solid var(--primary-color);
  border-radius: 0;
  background-color: transparent; 
  box-shadow: none;
  outline: 0 none transparent;
  -webkit-appearance:none;
}

textarea {
  margin-top: 40px;
  min-height: 200px;
  resize: none;
}

.form-control:focus {
  box-shadow: none;
  border-bottom-color: #e1e1e1;
}

::-moz-placeholder,
::-webkit-input-placeholder {
  font-size: 16px;
  color: var(--tertiary-color)
}

#myBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: rgba(229,152,155, 0.9);
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
}

#myBtn:hover {
  background-color: #555;
}

/* Hero/Header */

header {
  padding: 20px 35px;
  background-color: var(--tertiary-color);
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  background-image: url("/src/assets/cover/hero.png");
  background-size: cover;
  background-attachment: fixed;
  background-position: 80%;
  z-index: 9999;
  border-bottom: solid 5px rgba(229,152,155, 0.5);
  
}

header a {
  text-decoration: none;
  color: #212121;
}

header nav {
  margin: 7px 0;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

header nav ul li a {
  padding: 10px 15px;
  font-weight: lighter;
  font-size: 1.55vw;
  text-shadow: 0 0 10px rgba(229,152,155, 0.5);
}

header nav ul li a:hover {
  color: var(--tertiary-color);
  text-shadow: none;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link {
  color: antiquewhite;
}

.navbar-light .navbar-nav .nav-link {
  color: #212121;
}

.navbar-light .navbar-nav .nav-link:hover{
  color: var(--tertiary-color);
  text-shadow: none;
}




.hero {
  background-image: url('/src/assets/cover/hero.png');
  background-size: cover;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: flex-start;
  background-attachment: fixed;
  background-position: 80%;
}

.hero-cta {
  width: 45%;
  text-align: left;
  margin: 3.5%;
  color: #fff;
  font-size: 18px;
  line-height: 1.2;
}

.hero-cta h2 {
  font-size: 75px;
  color: #212121;
  text-shadow: 0 0 10px rgba(229,152,155, 0.5);
}

.hero-cta p {
  font-size: 30px;
  color: #212121;
  text-shadow: 0 0 10px rgba(229,152,155, 0.5);
}

.me{
    width: 28%;
    margin-right: 3%;
    margin-bottom: 8%;
    margin-top: 3%;
    border: solid 15px rgba(229,152,155, 0.5);
    border-radius: 1px 10px 1px 10px;

}

.skills-container {
  padding: 100px;
}

.skill + .skill {
  margin-top: 50px;
}

.about-title{
  color: var(--primary-color);
  font-size: 50px;
  padding-top: 50px;
}

.resume-btn {
  background-color: var(--secondary-color);
}

.contact-btn {
  background-color: transparent;
  color: #e27396;
  border: 3px solid #e27396; 
}

.skill-title{
  color: var(--primary-color);
  font-size: 50px;
  padding-bottom: 50px;
}

.skill {
  border: solid 3px var(--secondary-color);
}

.do-title,
.contact-title {
  color: var(--primary-color);
  font-size: 50px;
  margin-bottom: 20px;
}

.section-services {
  position: relative;
  padding-top: 230px;
}

.section-services:before {
  content: '';
  background: url(/src/assets/cover/scoll-by.jpg) no-repeat fixed center / cover;
  width: 100%;
  height: 400px; 
  position: absolute;
  left: 0;
  top: 0;
}

.overlay:after {
  content: '';
  width: 100%;
  height: 400px; 
  position: absolute;
  background-color: rgba(255,255,255,0.5);
  top: 0;
  left: 0;
  z-index: 0;
}

.section-services .container {
  padding: 120px 100px;
  position: relative;
  background-color: #fff;
  box-shadow: 0px 0px 40px 8px rgba(33,33,33, 0.4);
  z-index: 1;
}

.service h4 {
  margin: 25px 0;
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-color);
}

.service p {
  color: #212121;
  margin-bottom: 0;
}

.section-portfolio {
  padding-top: 120px;
  
}

.portfolio-title {
  font-size: 50px;
  color: var(--primary-color);
  padding-bottom: 20px;
}

.portfolio-item {
  height: 280px;
  margin-bottom: 30px;
  position: relative;
}

.portfolio-item:before {
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.portfolio-item:hover:before{
  background-color: rgba(181,131,141,0.9);
}

.portfolio-item-info {
  position: absolute;
  top: 80px;
  left: 10px;
  z-index: 999;
  opacity: 0;

}

.portfolio-item:hover .portfolio-item-info {
  left: 50px;
  opacity: 1;
}

.portfolio-item-info h4 {
  font-weight: 700;
  color: #fff;
}

.portfolio-item-info p {
  font-weight: 700;
  color: #fff;
}


.section-portfolio .btn {
  margin-top: 10px;
}

